.profile-form{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1em));
  gap: 2em;
  &__btn{
    grid-column: 1/-1;
    border-radius: 1em;
    height: 4em;
    margin-top: -.5em;
    span{
      font-size: 1em;
    }
  }
}

.file-input{
  display: grid;
  grid-template-columns: 8.75em calc(100% - 9.25em);
  gap: .5em;
  position: relative;
  &__preview{
    padding: .75em;
    border: 2px solid rgba(255, 255, 255, 0.24);
    border-radius: 0.75em;
    color: rgba(255, 255, 255, 0.60);
    position: relative;
    .text14{
      position: relative;
      z-index: 3;
      letter-spacing: -0.04813em;
    }
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: inherit;
      opacity: 0;
      transition: all .3s;
    }
  }
  &__no-img{
    width: 100%;
    height: 100%;
    @include center;
    svg{
      width: 4em;
      height: auto;
      max-width: 90%;
    }
    path{
      fill: #fff;
    }
  }
  &__area{
    position: relative;
    min-height: 8.75em;
    &-inner{
      @include center;
      flex-direction: column;
      text-align: center;
      border-radius: 0.75em;
      border: 2px dashed rgba(255, 255, 255, 0.24);
      height: 100%;
      padding: 1em;
    }
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      cursor: pointer;
      &:hover{
        &~.file-input__area-inner{
          border-color: #fff;
        }
      }
    }
  }
  &__icon{
    width: 1.75em;
    margin-bottom: .56em;
  }
  &__desc{
    margin-top: .75em;
  }
  &__name{
    display: none;
  }
  .custom-file-container{
    position: absolute;
    top: 0;
    right: 0;
    width: calc(100% - 9.25em);
    height: 100%;
    &:hover{
      &~.file-input__area-inner{
        border-color: #fff;
      }
    }
  }
  .label-container{
    display: none;
  }
  .input-container{
    margin: 0;
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    min-width: unset;
    display: block;
    right: 0;
    top: 0;
    input{
      cursor: pointer;
      min-width: unset;
      width: 100%;
      height: 100% !important;
    }
  }
  .image-preview{
    position: absolute;
    right: 100%;
    margin-right: .5em;
    top: 0;
    width: 8.75em;
    height: 8.75em;
    border-radius: 0.65em;
    opacity: 0;
  }
  &._uploaded{
    .image-preview{
      opacity: 1;
    }
    .file-input__preview{
      border: none;
      img{
        opacity: 1;
      }
    }
  }
  &._music-input{
    grid-template-columns: 100%;
    &._uploaded{
      .file-input__icon,
      .file-input__title,
      .file-input__desc{
        display: none;
      }
      .file-input__name{
        display: block;
      }
    }
  }
}

.passport-data{
  margin-top: 1.5em;
  @include grid100gap(1em);
  &:first-child{
    margin-top: 0;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    &._type2{
      grid-template-columns: 9.6875em calc(100% - 10.6875em);
    }
  }
}


@media screen and (max-width: 992px){
  .profile-form{
    grid-template-columns: 100%;
  }
}


@media screen and (max-width: 576px){
  .passport-data{
    &__grid{
      &._type2{
        grid-template-columns: 100%;
      }
    }
  }
}
