@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1em;
    height: 2.75em;
    font-weight: 500;
    border-radius: 0.75em;
    &:focus {
        box-shadow: none;
    }
    img{
        flex-shrink: 0;
    }
    svg{
        height: auto;
    }
    span{
        font-size: .875em;
        line-height: 121%;
        letter-spacing: -0.02625em;
    }
}

.m-btn-big{
    height: 3.75em;
    span{
        font-size: 1em;
    }
}

.m-btn-gradient{
    background: $gradient;
    color: #FFF;
    &:hover{
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.12) 0%, rgba(0, 0, 0, 0.12) 100%), linear-gradient(92deg, #3047FF 0%, #A341FF 97.78%);
        color: #FFF;
    }
}

.m-btn-white{
    background: #FFF;
    color: #000;
    &:hover{
        background: #C5C5C5;
        color: #000;
    }
}

.m-btn-blue{
    background: $primary;
    color: #fff;
    &:hover{
        background: #3646CD;
        color: #fff;
    }
}

.m-btn-dashed{
    border: 2px dashed rgba(255, 255, 255, 0.24);
    color: #fff;
    &:hover{
        border-color: #fff;
        color: #fff;
    }
}

.m-btn-gray{
    padding: 0 .5em;
    border-radius: 0.5em;
    background: rgba(255, 255, 255, 0.12);
    height: 2.3125em;
    display: flex;
    align-items: center;
    color: #fff;
    &._disabled{
        pointer-events: none;
        opacity: .4;
    }
    img{
        width: 1em;
        margin-right: .5em;
        flex-shrink: 0;
    }
    .text14{
        @include lastM(0 .57em 0 0);
    }
    &:hover{
        background: rgba(255, 255, 255, 0.24);
        color: #fff;
    }
}
