.auth{
  flex-grow: 1;
  background-image: url(../img/login-bg.webp);
  background-size: cover;
  background-position: center;
  padding: 2.5em 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  &__logo{
    width: 10.625em;
    display: block;
    margin-bottom: 1.28em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__c{
    margin-top: 1.28em;
    text-align: center;
    color: rgba(255, 255, 255, 0.60);
  }
}

.auth-form{
  width: 28.75em;
  max-width: 100%;
  padding: 2em;
  border-radius: 1.5em;
  background: rgba(255, 255, 255, 0.04);
  backdrop-filter: blur(32px);
  margin: auto 0;
  @include grid100gap(1em);
  &__title{
    text-align: center;
    margin-bottom: .75em;
  }
  &__btn{
    margin-top: .5em;
  }
  &__hint{
    text-align: center;
  }
  &__bottom{
    @include center;
  }
  &__link{
    margin-left: .94em;
    color: $primary;
    &:hover{
      color: #fff;
    }
  }
  &__forgot-pass{
    margin-top: .25em;
    margin-right: auto;
    display: inline-flex;
    font-weight: 600;
    color: $primary;
    background-color: transparent;
    &:hover{
      color: #fff;
    }
    span{
      font-size: .875em;
    }
  }
}

.type-radios{
  gap: 1em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .5em));
}

.type-radio{
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border-radius: 1.25em;
    border: 2px solid rgba(255, 255, 255, 0.12);
    padding: 1.25em;
    color: rgba(#fff,.6);
    transition: all .3s;
    svg{
      width: 2.0625em;
      height: auto;
      opacity: .6;
      margin-bottom: 1em;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 119%;
    font-weight: 700;
  }
  input:checked~.type-radio__inner{
    border-color: rgba(70, 91, 255, 0.60);
    color: #fff;
    svg{
      opacity: 1;
    }
    path{
      fill: $primary;
    }
  }
}


@media screen and (max-width: 576px){
  .auth-form{
    padding: 1.5em 1em;
  }
}
