.header{
  display: none;
  &__logo{
    width: 6.8125em;
    display: block;
    flex-shrink: 0;
    margin-right: auto;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: .75em 1em;
    position: fixed;
    z-index: 21;
    top: 0;
    width: 100%;
    background: $black;
    &~.content{
      padding-top: 4.25em;
    }
    &__user{
      //position: relative;
      &._open{
        .sidebar-user__dropdown{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    .sidebar-user__dropdown{
      width: calc(100% - 2em);
      left: 1em;
      margin-top: 0;
    }
  }

  .burger{
    width: 1.75em;
    height: 1.75em;
    position: relative;
    margin-left: .75em;
    svg{
      @include centerAbsolute;
      width: 100%;
      height: auto;
    }
    ._view2{
      opacity: 0;
    }
  }

  ._open-menu{
    .burger{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
    .sidebar{
      transform: translateX(0);
    }
  }
}
