.modal{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(37, 38, 39, 0.90);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
}

.modal {
    overflow-y: auto;
    padding: 2em 0;
    text-align: center;
    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog{
        text-align: left;
        vertical-align: middle;
    }
    &__dialog {
        border-radius: 1.5em;
        background: #101010;
        //backdrop-filter: blur(16px);
        width: 30em;
        max-width: calc(100% - .5em);
        position: relative;
        z-index: 3;
        padding: 2em;
        display: inline-grid;
        grid-template-columns: 100%;
        gap: 2em;
    }
    &__close {
        z-index: 10;
        border-top: 0;
        background-color: transparent;
        width: 1.5em;
        @include center;
        position: absolute;
        top: 1.37em;
        right: 1.13em;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
    &__top{
        text-align: center;
    }
}

.modal-form{
    @include grid100gap(1em);
    &__nav{
        margin-top: .25em;
        .m-btn{
            width: 100%;
        }
    }
}

.track-modal{
    .modal__dialog{
        width: 39.125em;
        padding: 2.5em;
    }
    .modal__top{
        text-align: left;
    }
}

.track-version,
.track-mate{
    @include grid100gap(.75em)
}

.radio-buttons{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 2.25em / 4));
    gap: .75em;
}

.radio-btn{
    label{
        height: 2.6875em;
        @include center;
        border-radius: 1em;
        background: rgba(255, 255, 255, 0.12);
        color: rgba(255, 255, 255, 0.60);
        span{
            font-size: 1em;
            line-height: 120%;
            letter-spacing: -0.055em;
        }
    }
    input:checked~label{
        background: #FFF;
        color: #000;
    }
}

.track-mate{
    .radio-buttons{
        grid-template-columns: repeat(3,calc(100% / 3 - .5em));
    }
}

.tariff-modal{
    .modal__dialog{
        width: 47.5em;
    }
    .modal__top{
        text-align: left;
    }
}

.tariffs{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
}


.tariff-card{
    &__inner{
        height: 100%;
        display: flex;
        flex-direction: column;
        border-radius: 1.5em;
        border: 2px solid rgba(255, 255, 255, 0.12);
        padding: 1.5em;
    }
    &__top{
        @include grid100gap(.31em);
    }
    &__price{
        color: rgba(255, 255, 255, 0.80);
    }
    &__items{
        margin-top: 1.5em;
        @include grid100gap(1em);
        padding-left: .625em;
        li{
            font-size: 1em;
            line-height: 150%;
            padding-left: 1em;
            position: relative;
            &:before{
                content:"\2022";
                position: absolute;
                top: 0;
                left: 0;
            }
        }
    }
    &__nav{
        margin-top: auto;
        padding-top: 1.69em;
        .m-btn{
            width: 100%;
        }
    }
    input._selected,
    input:checked{
        & ~ .tariff-card__inner{
            border-color: rgba(70, 91, 255, 0.60);
            background: rgba(70, 91, 255, 0.12);
            .tariff-card{
                &__nav{
                    .m-btn{
                        background: rgba(70, 91, 255, 0.24);
                        pointer-events: none;
                    }
                }
            }
        }
    }
}

.price-radios{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: 1em;
}

.price-radio{
    &__inner{
        padding: 1em;
        height: 100%;
        border-radius: 1.5em;
        background: rgba(255, 255, 255, 0.04);
        @include grid100gap(.75em);
        text-align: center;
        font-weight: 700;
        &:before{
            content: '';
            position: absolute;
            top: .75em;
            right: .94em;
            border: .125em solid rgba(255, 255, 255, 0.40);
            width: 1.25em;
            height: 1.25em;
            border-radius: 50%;
            transition: all .100s;
            background-image: url(../img/check.svg);
            @include bgImgSize(0);
        }
    }
    &__title{
        font-size: 1.25em;
        line-height: 120%;
        letter-spacing: -0.06875em;
    }
    &__desc{
        font-size: 1em;
        line-height: 119%;
        color: rgba(255, 255, 255, 0.60);
    }
    input._selected~.price-radio__inner,
    input:checked~.price-radio__inner {
        &::before {
            border-color: $primary;
            background-color: $primary;
            background-size: .75em auto;
        }
    }
}


@media screen and (max-width: 992px){
    .track-modal{
        .modal__dialog{
            padding: 1.25em;
        }
    }

    .tariff-card{
        &__items{
            font-size: .875em;
        }
        &__nav{
            padding-top: 1.25em;
        }
    }
}


@media screen and (max-width: 576px){
    .radio-buttons{
        grid-template-columns: repeat(2,calc(50% - .375em));
    }

    .track-mate{
        .radio-buttons{
            grid-template-columns: repeat(3,calc(100% / 3 - .5em / 3));
            gap: .25em;
        }
    }

    .radio-btn{
        label{
            height: 2.5625em;
            span{
                font-size: .875em;
            }
        }
    }

    .tariff-modal,
    .track-modal{
        .modal__dialog{
            padding: 1.25em;
            gap: 1.5em;
        }
    }

    .tariffs{
        width: calc(100% + 2.5em);
        margin-left: -1.25em;
        padding: 0 1.25em 1em;
        overflow-x: auto;
        display: flex;
        gap: unset;
        margin-bottom: -1em;
    }

    .tariff-card{
        width: 15.25em;
        flex-shrink: 0;
        @include lastM(0 1em 0 0);
    }

    .price-radios{
        grid-template-columns: 100%;
    }

    .price-radio{
        &__inner{
            min-height: 6.62em;
            padding: 1.72em;
            br{
                display: none;
            }
        }
        &__title{
            font-size: 1.125em;
        }
        &__desc{
            font-size: .875em;
        }
    }
}
