.page-top{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.75em;
  &__selects{
    display: flex;
    align-items: center;
    .m-select{
      @include lastM(0 1em 0 0);
    }
  }
}

.release-items{
  @include grid100gap(.75em);
}

.release-card{
  border-radius: 0.75em;
  background: rgba(255, 255, 255, 0.10);
  padding: 1em;
  display: grid;
  grid-template-columns: 18.8125em 12em calc(100% - 38.8125em);
  gap: 4em;
  align-items: center;
  &__col1{
    display: flex;
    align-items: center;
  }
  &__img{
    border-radius: 0.675em;
    position: relative;
    margin-right: 1.44em;
  }
  .avatar{
    width: 4.5em;
    height: 4.5em;
  }
  &__artist{
    color: rgba(255, 255, 255, 0.80);
  }
  &__name{
    margin-top: .25em;
  }
  &__status,
  &__listen,
  &__ups,
  &__date,
  &__bottom{
    display: flex;
    align-items: center;
  }
  &__bottom{
    margin-top: .5em;
  }
  &__status,
  &__date,
  &__listen{
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__date{
    margin-right: 1em;
    color: rgba(255, 255, 255, 0.60);
  }
  &__btn{
    color: rgba(#fff,.6);
    padding: .1875em 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.24);
    border-top: 1px solid transparent;
    svg{
      opacity: .6;
      width: 1em;
      height: auto;
      margin-right: .25em;
    }
    &:hover{
      color: #fff;
      border-bottom-color: #fff;
      svg{
        opacity: 1;
      }
    }
  }
  &__stat{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &-link{
      margin-top: .25em;
    }
  }
  &__status{
    margin-top: .5em;
    color: #58DD77;
    &._red{
      color: #FF5E5A;
    }
    &._orange{
      color: #FD880A;
    }
    &._gray{
      color: #A1A1A1;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__btn2{
    @include lastM(0 .5em 0 0);
  }
}

.play-btn{
  @include centerAbsolute;
  @include center;
  width: 1.25em;
  z-index: 2;
  svg{
    width: 100%;
    height: auto;
  }
  &:hover{
    path{
      fill: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .page-top{
    &__selects{
      .m-select{
        @include lastM(0 .25em 0 0);
      }
    }
  }

  .release-card{
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
    &__nav{
      justify-content: unset;
    }
    &__stat{
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      &-link{
        order: 2;
        margin: .5em 0 0;
      }
    }
    &__status{
      margin: 0 0 0 auto;
      width: 7em;
      justify-content: flex-end;
    }
    &__listen{
      width: calc(100% - 7em);
      flex-grow: 1;
    }
  }
}


@media screen and (max-width: 576px){
  .page-top{
    @include grid100gap(1em);
    margin-bottom: 1.25em;
    &__selects{
      display: grid;
      grid-template-columns: 7.4375em calc(100% - 7.4375em - .25em);
      gap: .25em;
      .m-select{
        @include lastM(0);
        .nice-select{
          width: 100%;
        }
        &:last-child{
          .list{
            left: unset;
            right: 0;
          }
        }
      }
    }
  }

  .release-card{
    padding: .75em;
    grid-template-columns: 100%;
    &__col2{
      margin-top: .25em;
    }
    &__btn2{
      height: 2em;
      padding: 0 .375em;
      @include lastM(0 .25em 0 0);
      .text14{
        font-size: .75em;
      }
    }
  }

  ._hide-mob{
    display: none;
  }
}
