.artist-data{
  margin-top: 1em;
  @include grid100gap(1em);
  &:first-child{
    margin-top: 0;
  }
}

.tokenfield{
  &.tokenfield-mode-tokens{
    font-size: 1em;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    border-radius: 0.75em;
    background: rgba(255, 255, 255, 0.08);
    border: none;
    height: auto;
    min-height: 3.75em;
    color: #fff;
    //padding: 0;
  }
  .tokenfield-input{
    width: auto !important;
    min-width: 18em;
    height: auto;
    background-color: transparent;
    float: unset;
    color: #fff;
    font-size: 1em;
    &::placeholder{
      color: rgba(255, 255, 255, 0.60);
    }
  }
  ._added-tag ~.tokenfield-input{
    min-width: unset;
  }
  &-set{
    &-item{
      border-radius: 0.5em !important;
      background: rgba(70, 91, 255, 0.24) !important;
      padding: 0 .5em !important;
      height: 2.25em !important;
      display: flex;
      align-items: center;
      .item-remove,
      .item-label{
        display: block !important;
        color: #fff !important;
        font-size: 1em;
        letter-spacing: -0.055em;
      }
      .item-remove{
        font-weight: 600 !important;
        margin-left: .5em !important;
        font-size: 1.25em !important;
        transform: translateY(-.125em);
      }
    }
  }
  .tokenfield-suggest{
    margin-top: .6875em;
    box-sizing: border-box;
    border-radius: 0.75em;
    background: #393939;
    margin-left: 0;
    border: none;
    padding: .5em;
    &-list{
      @include grid100gap(.25em);
    }
    &-item{
      border-radius: 0.75em;
      padding: .25em !important;
      min-height: 2.75em;
      display: flex;
      align-items: center;
      color: transparent;
      &.selected{
        background: rgba(255, 255, 255, 0.08);
      }
      &:before{
        content: attr(title);
        color: #fff;
        padding: 0 .5em;
        height: 2.25em;
        display: flex;
        align-items: center;
        border-radius: 0.5em;
        background: rgba(70, 91, 255, 0.24);
        letter-spacing: -0.055em;
      }
    }
  }
}

.air-datepicker{
  background: #393939;
  color: #fff;
  border: none;
  &:before,
  &:after{
    display: none;
  }
  .-other-month-{
    color: rgba(#fff,.4) !important;
  }
  &-nav{
    border-bottom-color: rgba($black,.25);
  }
  .-focus-{
    color: $black !important;
  }
}

.air-datepicker--pointer{
  background: red;
  display: none;
}

.release-form{
  row-gap: 1em;
  .profile-form__btn{
    margin-top: .5em;
  }
  .m-check{
    margin-top: .25em;
  }
}

._full{
  grid-column: 1/-1;
}

.select-track{
  margin-top: .5em;
  &__items{
    margin-top: 1.25em;
    @include grid100gap(.75em);
  }
}

.track-card2{
  justify-content: unset;
  .avatar{
    width: 4em;
    height: 4em;
  }
  .play-btn{
    width: 1.125em;
  }
  &__order{
    display: flex;
    align-items: center;
    margin-right: 1.44em;
    &-btn{
      width: 1.25em;
      flex-shrink: 0;
      margin-right: .75em;
      &:hover{
        path{
          fill: $primary;
        }
      }
      svg{
        width: 100%;
        height: auto;
      }
    }
  }
  &__nav{
    margin-left: auto;
    padding-left: 1em;
    display: flex;
    align-items: center;
  }
  .release-card__status{
    margin: 0 2em 0 0;
    width: auto;
  }
  &__remove{
    width: 1.5em;
    @include center;
    &:hover{
      path{
        fill-opacity: 1;
      }
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.track-add{
  height: 4.0625em;
  img{
    width: 1.5em;
    margin-right: .625em;
  }
}

.player{
  position: fixed;
  bottom: 0;
  right: 0;
  width: calc(100% - 14.75em);
  padding: 1.5em 14.75em 1.5em 0;
  background: #1A1A1A;
  z-index: 10;
  &__inner{
    padding: 0 2.75em;
  }
  &__inner,
  &__left{
    display: flex;
    align-items: center;
  }
  &__left{
    flex-shrink: 0;
    max-width: 13.125em;
  }
  &__btn{
    width: 2em;
    flex-shrink: 0;
    margin-right: .75em;
    &:hover{
      path{
        fill: $primary;
      }
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__artist{
    color: rgba(255, 255, 255, 0.80);
  }
  &__nav{
    flex-grow: 1;
    display: flex;
    align-items: center;
    margin: 0 1.5em;
  }
  &__time{
    font-size: 0.6875em;
    line-height: 120%;
  }
  &__progress{
    flex-grow: 1;
    background: #4B4B4B;
    height: .375em;
    width: 2em;
    margin: 0 .81em;
    position: relative;
    span{
      position: absolute;
      top: 0;
      left: 0;
      max-width: 100%;
      height: 100%;
      background: #D9D9D9;
    }
  }
}

.volume{
  display: flex;
  align-items: center;
  &__btn{
    width: 1.75em;
    @include center;
    pointer-events: none;
    &:hover{
      path{
        fill: $primary;
      }
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
  .player__progress{
    flex-grow: 0;
    width: 4.25em;
    margin: 0 0 0 .75em;
  }
}


@media screen and (max-width: 992px){
  .release-form{
    .profile-form__btn{
      margin-top: 1em;
    }
  }

  .track-card2{
    display: flex;
    flex-wrap: wrap;
    &__nav{
      width: 100%;
      margin: .75em 0 0;
      padding: 0;
      justify-content: space-between;
    }
    &__order{
      margin-right: .5em;
      &-btn{
        margin: 0;
      }
      &-num{
        display: none;
      }
    }
    .track-card__content{
      width: calc(100% - 1.75em);
    }
    .track-card__img{
      margin-right: .5em;
    }
    .track-card__artist{
      font-size: .8125em;
    }
    .track-card__name{
      font-size: 1em;
    }
    .avatar{
      border-radius: 0.45em;
      width: 3em;
      height: 3em;
    }
  }

  .player{
    padding: 1em;
    display: block;
    width: 100%;
    &__inner{
      padding: 0;
      flex-wrap: wrap;
      justify-content: space-between;
    }
    &__nav{
      width: 100%;
      margin: 1.5em 0 0;
      order: 2;
    }
  }
}


@media screen and (max-width: 576px){
  .volume{
    position: relative;
    &__btn{
      pointer-events: unset;
    }
    .player__progress{
      position: absolute;
      top: 100%;
      right: 0;
      margin: .25em 0 0;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
    &._open{
      .player__progress{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}
