.stat-page{
  &__top{
    display: flex;
    .avatar{
      width: 5.75em;
      height: 5.75em;
      margin-right: 1.75em;
    }
    .stat{
      flex-grow: 1;
      height: 100%;
    }
    .stat-card{
      min-height: 100%;
    }
  }
  &__nav{
    display: flex;
    margin-bottom: 1.75em;
  }
  &__back{
    width: 1.5em;
    @include center;
    margin-right: 1.5em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  &__title{
    span{
      color: rgba(255, 255, 255, 0.40);
    }
  }
  &__info{
    display: flex;
    align-items: center;
    margin-left: auto;
    .release-card__date{
      margin-left: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .stat-page{
    &__top{
      .avatar{
        width: 4.75em;
        height: 4.75em;
        margin-right: 1em;
      }
    }
    &__nav{
      margin-top: .75em;
      margin-bottom: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .stat-page{
    &__top{
      .avatar{
        display: none;
      }
    }
    &__nav{
      flex-wrap: wrap;
    }
    &__title{
      width: 100%;
      order: 3;
      margin-top: .67em;
    }
  }
}
