@import "_header";
@import "_footer";

.sidebar{
  background: rgba(255, 255, 255, 0.04);
  position: fixed;
  top: 0;
  left: 0;
  width: 14.75em;
  z-index: 20;
  &__inner{
    height: $vh;
    padding: 1.5em 1.25em;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
  }
  &__logo{
    width: 7.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__premium{
    flex-shrink: 0;
    margin-top: .75em;
    justify-content: flex-start;
    padding: 0 .5em 0 .75em;
    &-stars{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .5em;
    }
    &-arrows{
      margin-left: auto;
      width: 1em;
      flex-shrink: 0;
    }
  }
  &__release{
    flex-shrink: 0;
    margin-top: 2.5em;
    img{
      width: 1.5em;
      margin-right: .5em;
    }
  }
  &__links{
    margin-top: 1em;
    @include grid100gap(.5em);
  }
  &__link{
    display: flex;
    align-items: center;
    height: 2.5em;
    padding: 0 .75em;
    color: rgba(255, 255, 255, 0.60);
    position: relative;
    overflow: hidden;
    &:hover,
    &._active{
      color: #7F8DFF;
      &:before{
        opacity: 1;
        width: 2px;
      }
      &:after{
        width: 100%;
      }
      circle,
      path{
        fill: #828DFF;
      }
    }
    &:before,
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      transition: all .3s;
    }
    &:before{
      background-color: #808DFF;
      opacity: 0;
      z-index: 2;
      width: 0;
    }
    &:after{
      background: linear-gradient(90deg, rgba(48, 71, 255, 0.12) 0.34%, rgba(186, 123, 255, 0) 99.92%);
      width: 0;
    }
    span,svg{
      position: relative;
      z-index: 3;
    }
    span{
      font-size: .875em;
      letter-spacing: -0.0175em;
    }
    svg{
      width: 1.25em;
      margin-right: .625em;
    }
  }
  &__bottom{
    padding-top: 3em;
    margin-top: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__help{
    display: flex;
    align-items: center;
    svg{
      width: 1em;
      margin-right: .5em;
    }
    span{
      font-size: .875em;
      line-height: 120%;
    }
    &:hover{
      color: #7F8DFF;
      path{
        fill: #828DFF;
      }
    }
  }
  .socials{
    margin-top: 1.75em;
    padding: 0 .125em;
  }
  &__work-on{
    margin-top: 1.625em;
    display: flex;
    img{
      width: 4.0625em;
      margin-left: .5em;
      flex-shrink: 0;
    }
  }
  &__c{
    margin-top: .75em;
    color: rgba(255, 255, 255, 0.60);
    .text10{
      line-height: 180%;
    }
  }
}

.avatar{
  width: 2.5em;
  height: 2.5em;
  border-radius: .65em;
  flex-shrink: 0;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: inherit;
    display: block;
  }
}

.sidebar-user{
  z-index: 4;
  margin-top: 2.5em;
  position: relative;
  &._open{
    .sidebar-user__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    border-radius: 0.75em;
    background: rgba(255, 255, 255, 0.04);
    padding: .25em .5em .25em .25em;
    transition: all .3s;
    &:hover{
      background: rgba(255, 255, 255, 0.1);
      cursor: pointer;
    }
  }
  .avatar{
    margin-right: .75em;
  }
  &__info{
    @include grid100gap(.19em);
  }
  &__type{
    color: rgba(255, 255, 255, 0.80);
    display: flex;
    align-items: center;
    img{
      width: .75em;
      height: auto;
      margin-right: .25em;
    }
    &-duration,
    &-text{
      font-size: .625em;
      line-height: 120%;
    }
    &-duration{
      margin-left: .4em;
    }
  }
  &__arrow{
    width: 1em;
    @include center;
    background-color: transparent;
    margin-left: auto;
    &:hover{
      path{
        stroke: $primary;
      }
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__dropdown{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .625em;
    width: 100%;
    padding: .5em;
    border-radius: 0.75em;
    background: #2D2D2D;
    box-shadow: 0 .5em 4em 0 rgba(0, 0, 0, 0.60);
  }
  &__link{
    display: flex;
    align-items: center;
    padding: .56em .75em;
    min-height: 2.125em;
    span{
      font-size: .8125em;
      line-height: 123%;
      letter-spacing: -0.02438em;
    }
  }
  &__bottom{
    padding: .25em 0;
    border-top: 1px solid rgba(255, 255, 255, 0.12);
    margin-top: .5em;
    .sidebar-user__link{
      color: #D65C60;
      &:hover{
        color: darken(#D65C60,10%);
      }
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    border-radius: 0.375rem;
    background: rgba(255, 255, 255, 0.12);
    @include lastM(0 .5em 0 0);
    width: 1.75em;
    height: 1.75em;
    @include center;
    &:hover{
      background: rgba(255, 255, 255, 0.22);
    }
    img{
      width: 1.125em;
    }
    &._small{
      img{
        width: 1em;
      }
    }
  }
}

.content{
  padding: 2.5em;
  margin-left: auto;
  width: calc(100% - 14.75em);
}


@media screen and (max-width: 992px){
  .sidebar{
    background: #101010;
    padding-top: 3.75em;
    width: 100%;
    transition: all .4s;
    transform: translateX(-150%);
    z-index: 20;
    height: $vh;
    &__inner{
      height: 100%;
      padding: 1em .75em;
    }
    &__logo{
      display: none;
    }
    &__release{
      margin-top: .75em;
    }
    &__work-on,
    .socials{
      margin-top: 1.125em;
    }
  }

  .sidebar-user{
    margin: 0;
  }

  .content{
    padding: .25em 1em 2.5em;
    width: 100%;
  }
}
