.dashboard{
  display: grid;
  grid-template-columns: calc(69.8% - 2em) 30.2%;
  gap: 2em;

  @media screen and (min-width: 993px){
    .balance{
      padding: 1em;
      border-radius: 1.25em;
      margin-bottom: .5em;
      display: block;
      &__nav{
        margin-top: .5em;
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .25em));
        gap: .5em;
        .m-btn{
          padding: 0;
          @include lastM(0);
        }
      }
      &__nav{
        .m-btn{
          height: 3em;
          padding: 0 1.19em;
          @include lastM(0 .5em 0 0);
        }
      }
    }
    .report-card{
      padding: .75em;
      gap: unset;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      &__info{
        &:nth-child(1){
          padding-right: .5em;
        }
      }
      &__info2{
        width: 100%;
        margin-top: 1em;
        justify-content: unset;
      }
      &__badge{
        margin-right: .25em;
      }
      &__btn{
        span{
          font-size: .8125em;
        }
      }
    }
    .history{
      &__top{
        justify-content: unset;
        .month-select{
          margin-left: .75em;
          .list{
            max-height: 14.5em;
            overflow-y: auto;
          }
        }
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
  }
}

.more{
  margin-top: 1.25em;
  display: flex;
  justify-content: center;
  &__btn{
    height: 2.75em;
    padding: 0 1em;
    &:not(:hover){
      background: rgba(255, 255, 255, 0.40);
    }
  }
}

.search{
  min-width: 18.3125em;
  position: relative;
  &__btn{
    position: absolute;
    top: 50%;
    right: 1em;
    transform: translateY(-50%);
    width: 1.25em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  &__field{
    input{
      font-size: .875em;
      border: 1px solid rgba(255, 255, 255, 0.12);
      border-radius: .86em;
      height: 2.86em;
      padding: 0 3.5em 0 1.14em;
    }
  }
}

.stat{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - .5em));
  gap: .75em;
}

.stat-card{
  border-radius: 1.5em;
  background: rgba(255, 255, 255, 0.08);
  padding: 1.25em;
  display: flex;
  align-items: center;
  &__icon{
    width: 3em;
    flex-shrink: 0;
    margin-right: 1em;
  }
  &__content{
    font-weight: 600;
    @include grid100gap(.375em);
  }
  &__title{
    color: rgba(255, 255, 255, 0.60);
  }
}

.chart{
  margin-top: 2em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info{
    display: flex;
    align-items: center;
  }
  .hint-icon{
    margin-left: .625em;
  }
}

.hint-icon{
  width: 1.125em;
  @include center;
  svg{
    width: 100%;
  }
  &:not(:hover){
    opacity: .3;
  }
}

.chart-item{
  width: calc(100% + 1em);
  margin-left: -1em;
}

.apexcharts-tooltip-text-y-label{
  display: none;
}

.apexcharts-xaxis-tick,
.apexcharts-xcrosshairs{
  fill-opacity: 0 !important;
  stroke: transparent !important;
}
#SvgjsLine1073{
  stroke: #2A2A2A !important;
}

.popular-tracks{
  margin-top: 1.75em;
  &__items{
    margin-top: 1.25em;
    @include grid100gap(.75em);
  }
}

.track-card{
  display: flex;
  justify-content: space-between;
  border-radius: 0.75em;
  background: rgba(255, 255, 255, 0.10);
  padding: .75em;
  &__content{
    display: flex;
    align-items: center;
  }
  &__img{
    position: relative;
    margin-right: 1.44em;
    flex-shrink: 0;
  }
  .avatar{
    width: 3.25em;
    height: 3.25em;
  }
  .play-btn{
    width: .75em;
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__artist{
    color: rgba(255, 255, 255, 0.80);
  }
  .stat{
    display: flex;
    gap: unset;
  }
  .stat-card{
    @include lastM(0 .75em 0 0);
    padding: .375em .75em;
    border-radius: 0.75em;
    min-width: 10.75em;
    &__icon{
      width: 2em;
      margin-right: .75em;
    }
    &__title{
      font-size: .75em;
    }
    &__num{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .dashboard{
    grid-template-columns: 100%;
  }

  .stat{
    grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
    gap: .5em;
  }

  .stat-card{
    border-radius: 1em;
    padding: 1em;
    &__icon{
      width: 2.5em;
    }
    &__title{
      font-size: .75em;
    }
    &__num{
      font-size: 1.25em;
    }
  }

  .popular-tracks{
    margin-top: .75em;
  }

  .track-card{
    display: block;
    .stat{
      margin-top: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .search{
    width: 100%;
    min-width: unset;
  }

  .stat{
    grid-template-columns: 100%;
  }

  .track-card{
    &__artist{
      font-size: .75em;
    }
    &__name{
      font-size: .875em;
    }

    .stat{
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - .5em));
      gap: .75em;
    }
    .stat-card{
      min-width: unset;
      padding: 0 0 1em;
      background-color: transparent;
      border-radius: 0;
      position: relative;
      &__icon{
        width: 1.25em;
        margin-right: .25em;
      }
      &__num{
        font-size: .8125em;
        font-weight: 500;
      }
      &__title{
        position: absolute;
        bottom: 0;
        left: 0;
        font-size: .625em;
        font-weight: 500;
      }
    }
  }
}
