.finance{
  display: grid;
  grid-template-columns: calc(72.8% - 2em) 28.2%;
  gap: 2em;
  @media screen and (min-width: 993px){
    .tabs{
      display: none;
    }
    .tab-content{
      display: block;
    }
  }
}

.balance{
  padding: 1.5em;
  border-radius: 1.5em;
  background: $gradient;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2em;
  &__info{
    @include grid100gap(.19em);
  }
  &__nav{
    display: flex;
    .m-btn{
      height: 3.4375em;
      padding: 0 1.5em;
      @include lastM(0 1em 0 0);
      img{
        width: 1.5em;
        margin-right: .5em;
      }
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.reports{
  @include grid100gap(1em);
}

.report-card,
.operation-card{
  padding: 1em;
  border-radius: 0.75em;
  background: rgba(255, 255, 255, 0.08);
}

.report-card{
  display: grid;
  grid-template-columns: calc(60% - 10.75em - .875em) calc(40% - 10.75em - .875em)  21.5em;
  gap: .875em;
  &__info{
    @include grid100gap(.25em);
  }
  &__prof{
    color: rgba(255, 255, 255, 0.80);
  }
  &__info2{
    display: flex;
    justify-content: space-between;
  }
  &__badge{
    height: 2.25em;
    color: #8E9AFF;
    padding: 0 .625em;
    display: flex;
    align-items: center;
    border-radius: 0.375em;
    background: rgba(70, 91, 255, 0.12);
    img{
      width: 1.25em;
      flex-shrink: 0;
      margin-right: .25em;
    }
  }
  &__btn{
    padding: 0 .75em;
    height: 2.25em;
  }
}

.tabs{
  display: grid;
  grid-template-columns: 50% 50%;
  margin-bottom: 1em;
}

.tab{
  position: relative;
  padding: 1em 0;
  @include center;
  color: rgba(255, 255, 255, 0.60);
  &:hover{
    cursor: pointer;
    color: #fff;
  }
  &._active{
    color: #fff;
    &:before{
      background: $primary;
    }
  }
  &:before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: rgba(255, 255, 255, 0.24);
    transition: all .3s;
  }
  span{
    font-size: .875em;
    line-height: 121%;
  }
}

.history{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    ._view2{
      display: none;
    }
  }
  &__items{
    margin-top: 1em;
    @include grid100gap(.75em);
  }
}

.month-select{
  .nice-select{
    border: none;
    height: unset;
    padding: 0;
    &:before{
      background-image: url(../img/select-arrow2.svg);
      width: 1.25em;
      height: 1.25em;
      margin-left: .25em;
    }
    .current{
      color: $primary;
      font-size: .875em;
    }
    .list{
      left: unset;
      right: 0;
    }
  }
}

.operation-card{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__info{
    @include grid100gap(.19em);
  }
  &__num{
    white-space: nowrap;
    padding-left: .25em;
  }
}


@media screen and (max-width: 992px){
  .finance{
    grid-template-columns: 100%;
    gap: 0;
  }

  .balance{
    padding: 1em;
    border-radius: 1.25em;
    margin-bottom: .5em;
    &__nav{
      .m-btn{
        height: 3em;
        padding: 0 1.19em;
        @include lastM(0 .5em 0 0);
      }
    }
  }

  .history{
    &__top{
      justify-content: unset;
      .month-select{
        margin-left: .75em;
        .list{
          max-height: 14.5em;
          overflow-y: auto;
        }
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .balance{
    display: block;
    &__nav{
      margin-top: .5em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
      .m-btn{
        padding: 0;
        @include lastM(0);
      }
    }
  }

  .report-card{
    padding: .75em;
    gap: unset;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__info{
      &:nth-child(1){
        padding-right: .5em;
      }
    }
    &__info2{
      width: 100%;
      margin-top: 1em;
      justify-content: unset;
    }
    &__badge{
      margin-right: .25em;
    }
    &__btn{
      span{
        font-size: .8125em;
      }
    }
  }
}
