.m-select{
  .nice-select{
    font-size: 1em;
    height: 2.4375em;
    padding: 0 .75em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.5em;
    border: 1px solid rgba(255, 255, 255, 0.24);
    background-color: transparent !important;
    color: #FFF;
    font-weight: 500;
    width: auto;
    min-width: 100%;
    &:after{
      display: none;
    }
    &:before{
      content: '';
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
      flex-shrink: 0;
      margin-left: .5em;
      width: 1em;
      height: 1em;
      order: 10;
    }
    .current{
      font-size: .75em;
      display: block;
      max-width: calc(100% - 1.5em);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .list{
      box-shadow: 0 .5em 4em 0 rgba(0,0,0,.6);
      background: #393939;
      border-radius: .75em;
      padding: .5em;
      width: auto;
      min-width: 100%;
      @include grid100gap(.25em);
    }
    .option{
      border-radius: 0.75em;
      display: flex;
      align-items: center;
      font-size: .75em;
      padding: .78em 1em;
      border: none;
      &:hover,
      &.selected{
        background: rgba(255, 255, 255, 0.12);
      }
    }
  }
}

.fg {
  position: relative;
  margin: 0;
  textarea,
  input ,
  .nice-select{
    border-radius: 0.75em;
    background-color: rgba(255, 255, 255, 0.08);
    padding: 0 1em;
    height: 3.75em;
    width: 100%;
  }
  textarea,
  input {
    border: none;
    font-size: 1em;
    color: #fff;
    &::placeholder {
      color: rgba(255, 255, 255, 0.60);
    }
    &:focus{
      background-color: rgba(255, 255, 255, 0.18);
    }
  }
  .nice-select{
    background: rgba(255, 255, 255, 0.08) !important;
    border: none;
    &:focus{
      background: rgba(255, 255, 255, 0.18) !important;
    }
    .current{
      font-size: 1em;
    }
    .option{
      font-size: 1em;
    }
  }
  &__hint{
    margin-top: 1em;
    padding: .75em;
    border-radius: 0.75em;
    background: rgba(255, 180, 82, 0.12);
    display: flex;
    align-items: center;
    color: rgba(255, 255, 255, 0.60);
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .81em;
    }
    .text14{
      max-width: 27.5em;
      line-height: 143%;
    }
    &-text{
      color: rgba(255, 255, 255, 0.40);
      margin-top: .75em;
    }
  }
  textarea{
    resize: none;
    padding-top: 1.28em;
    padding-bottom: 1.28em;
    height: 16.375em;
  }
}

.fg-ai{
  &__btn{
    position: absolute;
    right: .56em;
    bottom: .94em;
    height: 2.75em;
    padding: 0 1em;
    border-radius: 1em;
  }
}

.date-input{
  input{
    padding-left: 2.75em;
    background-image: url(../img/calendar2.svg);
    background-size: 1.25em auto;
    background-repeat: no-repeat;
    background-position: left 1em center;
  }
}

.price-radio,
.tariff-card,
.radio-btn,
.m-check,
.type-radio{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check {
  input {
    &:checked~.m-check__item {
      &::before {
        border-color: $primary;
        background-color: $primary;
        background-size: .75em auto;
      }
    }
  }
  &__item {
    padding-left: 2.25em;
    position: relative;
    min-height: 1.25em;
    display: flex;
    align-items: center;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 0.25em;
      border: .125em solid rgba(255, 255, 255, 0.40);
      width: 1.25em;
      height: 1.25em;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
      background-image: url(../img/check.svg);
      @include bgImgSize(0);
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}


@media screen and (max-width: 992px){
  .m-select:not(.fg){
    .nice-select{
      padding: 0 .5em;
    }
  }
}
